import React, { Component } from "react";
import { connect } from "react-redux";
import { fetchIcons,fetchStyle,fetchCategories,changeCategories } from "../../actions/iconActions";
import { bindActionCreators } from "redux";
import IconsList from "./iconsList";
import styled from 'styled-components';
import {BlueButton} from '../common/buttons'
import Categories from './categories';
import dynamic from 'next/dynamic';
import { BREAKPOINT_LG,BREAKPOINT_MDS,BREAKPOINT_MDL,BUTTON_BOX_SHADOW,ACTIVE_NAV_LIST_COLOR,BOX_SHADOW_POPUP,BUTTON_RADIUS,SIDE_BOX_SHADOW,SIDE_WIDTH,RED_COLOR,ICONS_DARK_BACKGROUND,NAV_LIST_COLOR,SELECTED_ACTIVE_COLOR,FILTER_BACKGROUND,PADDING,BUTTON_COLOR,DARK_BACKGROUND,SELECTED_COLOR,FADE_FONT_COLOR,HEADER_COLOR,FLEX_ACTIVE,ICONS_BACKGROUND,BLUE_COLOR,LIGHT_BACKGROUND,MAX_WIDTH,DARK_BORDER_COLOR,BORDER_COLOR,SECONDARY_FONT_COLOR,PRIMARY_COLOR,FONT_COLOR, BREAKPOINT_MD, BREAKPOINT_SM, DARK_FILTER_BACKGROUND, BORDER_COLOR_DARK, DARK_FONT_COLOR} from '../types';
import SimpleBar from "simplebar-react";

import {VERSION} from '../../actions/types'
import OutsideClickHandler from 'react-outside-click-handler';
import Footer from '../common/footer';
import ToTop from '../common/ToTop';

const Icon = dynamic(
  () => import('./icon'),
  {
    loading: () => (<div className="mock-icon-box">
    <div className="mock-box-1"></div>
    <div className="mock-box-2"></div>
    <div className="mock-box-3"></div>
    </div>
    )}
);

const StyledIconCont = styled.div`
 padding: 25px;
 background:${FILTER_BACKGROUND};
      display: flex;
      pointer-events: auto;
      @media screen and (max-width: ${BREAKPOINT_MD}) {
          display: block;
          height: 48px;
          padding: 15px;
      }
           @keyframes placeHolderShimmer {
  0% {
    background-position: -468px 0
  }
  100% {
    background-position: 468px 0
  }
}
      .mock-icon-box {
         @media screen and (max-width: ${BREAKPOINT_LG}) {
             display:none;
             visibility:hidden;
            }
            width: 280px;
    height: 260px;
        display: flex;
        animation-duration: 1s;
  animation-fill-mode: forwards;
  animation-iteration-count: infinite;
  animation-name: placeHolderShimmer;
  animation-timing-function: linear;
  background: #f6f7f8;
  background: linear-gradient(to right, #eeeeee 8%, #dddddd 18%, #eeeeee 33%);
  background-size: 800px 104px;

  position: relative;
  div{
    background: ${FILTER_BACKGROUND};
    position: absolute;
  }
       .mock-box-1 {
        top: 170px;
    width: 280px;
    height: 20px;
}
.mock-box-2 {
  top: 215px;
    width: 280px;
    height: 20px;
}
.mock-box-3 {
  top: 235px;
    left: 160px;
    width: 120px;
    height: 94px;
}


    }
`;

const IconsSection = styled.div`
padding:0px;
background: ${ ICONS_BACKGROUND};

  @media screen and (max-width: ${BREAKPOINT_LG}) {
    padding: 0px;
  }
  &.active .icons-left-section {
    order: 0;
    
  }
`;
const IconsSectionCont = styled.div`
padding:${PADDING};

 @media screen and (max-width: ${BREAKPOINT_LG}){
  padding:0px;

}
 @media screen and (min-width: ${BREAKPOINT_LG}){
&.side-active .icon-box{
flex: 0 0 ${FLEX_ACTIVE}!important;
                max-width: ${FLEX_ACTIVE}!important;
}

}

 max-width: ${MAX_WIDTH};
  margin: 0 auto;
  @media screen and (min-width: ${BREAKPOINT_LG}){
  display:flex;}
  .icons-left-section {
    @media screen and (max-width: ${BREAKPOINT_MDL}) {
             display:none;
             visibility:hidden;
            }
  
    -webkit-animation-duration: 0.5s !important;
    animation-duration: 0.5s !important;
    
   
    position: fixed;
    z-index: 99997 ;
     -webkit-transform: translateZ(0);
        animation-fill-mode: forwards;
        top: 65px;
    bottom: 0;
    right: 0px;
    
    width: ${SIDE_WIDTH};
   
    transition: 0.4s ease;
    .left-section-inner{
      background: ${FILTER_BACKGROUND};
      overflow-y: auto;
    margin: 0 auto;
    display: block;
    border-radius: 2px;
    box-shadow: ${SIDE_BOX_SHADOW};
    margin-left: 15px;
    height: calc( 100vh - 62px );
      @media screen and (max-width: ${BREAKPOINT_LG}){
        border-radius: 0px;
        border-left: 0px;
        border-right: 0px;
        max-width: none;
        left: 0;
    right: 0;
      }
    }
    @media screen and (max-width: ${BREAKPOINT_LG}) {
       
    pointer-events: all;
     
    }
  }
  .icons-side-section {
    position: fixed;
    background: #fff;
    z-index: 99999;
    top: 20px;
    right: 20px;
    overflow-y: scroll;
    max-height: 200px;
    border-radius: ${BUTTON_RADIUS};
    box-shadow: ${BOX_SHADOW_POPUP};
    min-width: 112px;
    max-width: 280px;
    overflow: auto;
    -webkit-overflow-scrolling: touch;
    padding-top: 0;
    padding-bottom: 0;
    max-height: 256px;
    .side-section-inner {
      border-bottom:1px solid transparent;
 
    padding-left: 0px;
    @media screen and (max-width: ${BREAKPOINT_LG}) {
      border-bottom: 1px solid ${BORDER_COLOR};
           margin: 0 ;
        }
    }
    @media screen and (max-width: ${BREAKPOINT_SM}) {
      display:none;
      visibility:hidden;
    }
  }
 
  .icons-right-section {
   
    padding-top:90px;
    padding-left: 20px;
   
   width:100%;
    padding-right: 340px;
    @media screen and (max-width: ${BREAKPOINT_LG}) {
      width:auto;
    }
    @media screen and (max-width: ${BREAKPOINT_MDL}) {
      padding-right: 20px;
    }
    
    h2 {
    margin: 15px;
    margin-bottom: 30px;
    font-size: 36px;
    font-weight: 400;
    line-height: 48px;
    color: ${HEADER_COLOR};
    a{
      display:none!important;
      visibility:hidden;
      @media screen and (max-width: ${BREAKPOINT_LG}) {
        display:flex!important;
      visibility:visible;
      }
    }
    @media screen and (max-width: ${BREAKPOINT_MDS}) {
      font-size: 30px;
      line-height: 44px;
      margin: 10px;
    margin-bottom: 25px;
    }
    @media screen and (max-width: ${BREAKPOINT_SM}) {
      font-size: 24px;
    line-height: 36px;
    }
}
}
   
          
           
    .icon-list {
      padding: 0px;
      min-height: calc(100vh - 330px);
      padding-bottom: 130px;
      .container {
     
       
        
        .no-result {
            text-align: center;
            font-weight: 400;
            padding: 30px;
            min-height: 50px;
            color:  ${ SECONDARY_FONT_COLOR};
      
            font-size: 18px;
            margin-bottom: 0px;
            p {
              margin-top: 2px;
              font-size: 16px;
              color:  ${ SECONDARY_FONT_COLOR};
              font-style: italic;
            }
        }
      }
      @media screen and (max-width: ${BREAKPOINT_MD}) {
        padding: 0px;
      }
    }
  }
  

`;


const IconsFilter = styled.div`  
background: ${FILTER_BACKGROUND};

    box-sizing: border-box;
  
   .icon-filter-max{ max-width: ${MAX_WIDTH};
    margin: 0 auto;
 
    @media screen and (max-width: ${BREAKPOINT_LG}) {
      padding-top: 0px;
      margin-bottom: 0px;
    }
    .icons-filter-in {
          font-weight: 400;
       
          padding-bottom: 10px!important;
    padding-top: 20px!important;
          border-bottom: 1px solid rgba(0,31,51,0.1);
        .icons-count {
          align-self: flex-end;
          flex: 1;
          font-style:italic;
          text-align: left;
          padding-bottom:10px;
          transition:0.15s;
          @media screen and (max-width: ${BREAKPOINT_MD}) {
              display: none;
          }
        }
        .selected-category {
    display: flex;
    background:  #313133;
    @media screen and (max-width: ${BREAKPOINT_MD}) {
            display:none;
            visibility:hidden;
          }
    color: #fff;
    margin-left:10px;
    align-items: center;height: 28px;

transition:0.2s ease;
    justify-content: center;
    border-radius: 30px!important;
    &:active{
      box-shadow:0 0px 0 5px ${ LIGHT_BACKGROUND};
           }
    div {
    padding: 0px!important;
    line-height: 18px!important;
    height: 20px!important;
}.selected-category-name {
    color: #fff!important;
}
.selected-category-x i {
    color: #fff!important;
    font-size: 12px!important;
    margin-left: 10px;
}
}
.icons-theme{
  @media screen and (max-width: ${BREAKPOINT_MD}) {
            display:none;
            visibility:hidden;
          }
  flex:0 0 36px;
          width: 36px;
    height: 36px;
    margin-bottom: 4px;
    align-items: center;
    box-shadow: 0 0px 0 2px #f9f9f9;
    justify-content: center;
    transition:0.2s  ease;
          margin-left:0px;
          display: flex;
          padding:0px;
          border-radius:30px;
          div {
    width: 12px!important;
    height: 12px!important;
    background:#313133;
         
    padding: 0!important;
    border-radius: 50%!important;
}
    @media screen and (max-width: ${BREAKPOINT_MD}){
             margin-bottom: 7px;
           }
           @media screen and (max-width: ${BREAKPOINT_SM}) {
            display:none;
            visibility:hidden;
          }
  &:active{
          box-shadow:0 0px 0 5px ${ LIGHT_BACKGROUND};
        
          
         }
}
        .icons-filter {
          align-self: flex-end;
          flex:1;
          @media screen and (max-width: ${BREAKPOINT_MD}) {
            display:none;
            visibility:hidden;
          }
          line-height: 30px;     
    height: 30px;
          text-align: left;
          padding: 0px;
          padding: 5px 15px;
    padding-top: 0px;
        font-size:16px;
          margin-bottom: 4px;
           @media screen and (max-width: ${BREAKPOINT_MD}){
             margin-bottom: 7px;
           }
          transition:0.2s  ease;
          margin-left:0px;
          
          border-radius:${BUTTON_RADIUS};
          border:1px solid transparent;
          background:${props => props.side ?    LIGHT_BACKGROUND : 'transparent'};
      
        
          i{
            color:#313133;
          }
         &:active{
          box-shadow: ${BUTTON_BOX_SHADOW};
        
          
         }
        }
        i {
          font-size: 16px;
          color:#313133;
          float:right;
          line-height:30px;

        }
        display: block;
            padding:${PADDING};
    margin: 0px;
  
        @media screen and (max-width: ${BREAKPOINT_LG}) {
           margin: 0 ;
           padding:15px;
               padding-bottom: 10px;
        }  
        .filter-top, .filter-bottom {
    display: flex;padding: 0;
}
.filter-bottom{
  border-radius:${BUTTON_RADIUS};
  overflow:hidden;
    border: 1px solid ${BORDER_COLOR};
    div {
    flex: 1;
    text-align: center;
}
}
.filter-top {
  
    border-radius: 0px!important;
 
    margin-top: 15px!important;
}
        div{
          @media screen and (max-width: ${BREAKPOINT_MD}) {
            margin: 0 5px;
            padding: 5px 10px;
          }
                 padding: 5px 15px;
    margin: 0px;
    font-size: 14px;
    font-weight:400;   transition: 0.2s;
    border-radius:5px;
          cursor: pointer;
          color: ${ NAV_LIST_COLOR};

          -webkit-touch-callout: none;
          -webkit-user-select: none;
          -khtml-user-select: none;
          -moz-user-select: none;
          -ms-user-select: none;
          user-select: none;
          &.active {
            background: #f9f9f9;
            color:${ACTIVE_NAV_LIST_COLOR};
            border-radius: 0px;
       
          }
          &:last-child{
          }
          &:first:child{
            margin-right: 0px;
          }
        }

      }}

`;
import classNames from "classnames";
import { changeSideIcon, setSelectedIcon,setCategory,setSearchTerm } from '../../actions/iconActions';
import Link from 'next/link';
class IconsSet extends Component {
  constructor(props) {
    super(props);

    this.state = {
      icons: {
        data: null
      },
      categories: {
        data: null
      },
      showSide:false,
      view:'grid',
      noResult: false,
      selectedIcon: {
        name: null,
        slug: null,
        type_of_icon:null
      },
      category:null,
      style:'ALL',
      displaySideIcon: false,
      search: "",
      dark:false
      
    };
    this.handleBlur = this.handleBlur.bind(this);
    this.handleSide = this.handleSide.bind(this);
    this.handleFilter = this.handleFilter.bind(this);
this.handleKeyPress = this.handleKeyPress.bind(this);

  
  }
 renderIcon(){
  
  if (this.state.selectedIcon.name != null) {
    return (
      <div>
         <Icon icon={this.state.selectedIcon} />
      </div>
    )
  }
  return '';
    
 
 }
  handleSide(){

    
    if(!this.state.showSide){
      var el = document.querySelector(".icons-filter-in");
      var boundingRect = el.getBoundingClientRect();
       window.scroll({
  top: (boundingRect.top+window.pageYOffset)-60,  
  behavior: 'smooth' 
});
    }
    this.props.changeCategories(!this.state.showSide)
  }
  
   handleBlur(){
    }
   
    handleFilter(style){
      this.setState({
        style
      })
    if (style==='REGULAR' || style==='SOLID'|| style==='LOGO' ){
this.props.fetchStyle(style,this.state.search,this.state.category);

    }
    else{
this.props.fetchIcons(this.state.search,this.state.category);
      }
    
    }
    handleKeyPress(event){
      if(this.state.icons.data!==null){
  if(event.keyCode  == 39){ 
    if(this.state.selectedIcon.name!=null){

     
      var index = this.state.icons.data.findIndex(p => p.slug == this.state.selectedIcon.slug);
 
   if(index === (this.state.icons.data.length)-1)
   {
    index=-1;
   }
    this.props.changeSideIcon(true);
      this.props.setSelectedIcon(this.state.icons.data[index+1].name,this.state.icons.data[index+1].slug,this.state.icons.data[index+1].type_of_icon);
  
    }
    
  }
  else if(event.keyCode  == 37){
  if(this.state.selectedIcon.name!=null){

     
      var index = this.state.icons.data.findIndex(p => p.slug == this.state.selectedIcon.slug);
 
   if(index === 0)
   {
    index=this.state.icons.data.length;
   }
    this.props.changeSideIcon(true);
      this.props.setSelectedIcon(this.state.icons.data[index-1].name,this.state.icons.data[index-1].slug,this.state.icons.data[index-1].type_of_icon);
  
    }
}}}
  componentWillReceiveProps(newProps) {
    this.setState({
      displaySideIcon: newProps.displaySideIcon
    });
this.setState({
      showSide: newProps.displayCategories
    });

    if(newProps.icons.data!==null && newProps.icons.data.length===0){
this.setState({
      noResult: true
    });
    }
    else{
      this.setState({
      noResult: false
    });
    }

   
    this.setState({
      selectedIcon: newProps.selectedIcon,
      search: newProps.search,
      category: newProps.category,
      icons: newProps.icons
    });
    
  }
  componentWillMount() {
    this.props.fetchIcons("",this.state.category);
  }
componentWillUnmount() {
    this.props.setSearchTerm("");
    this.props.setCategory(null);
   
 
  }
   componentDidMount() {
      
    }
    

  render() {



    var sectionClass = classNames({
      active: this.state.displaySideIcon,
    });
    var sectionContClass = classNames({
      'side-active': this.state.showSide,
    });
     
    return (
     
        <IconsSection  className={sectionClass} onKeyDown={this.handleKeyPress} >
      <ToTop side={this.state.displaySideIcon}/>
      
         <IconsSectionCont displaySideIcon={this.state.displaySideIcon} side={this.state.showSide} className={sectionContClass}>
          <div className="icons-left-section ">
            <div className="left-section-inner"  onBlur={this.handleBlur} >
            <IconsFilter   side={this.state.showSide}>
         <div className="icon-filter-max">
         <div className="icons-filter-in">
           <div className="filter-bottom">
         <div className={this.state.style==='ALL'?"filter-all active":"filter-all"} onClick = {()=>this.handleFilter('ALL')}>
         All
           </div>
           <div className={this.state.style==='REGULAR'?"filter-regular active":"filter-regular"} onClick = {()=>this.handleFilter('REGULAR')}>
           Regular
           </div>
           <div className={this.state.style==='SOLID'?"filter-solid active":"filter-solid"} onClick = {()=>this.handleFilter('SOLID')}>
           Solid
           </div>
           <div className={this.state.style==='LOGO'?"filter-logo active":"filter-logo"} onClick = {()=>this.handleFilter('LOGO')}>
           Logos
           </div>
           </div>
           <div className="filter-top">
      
      <div className="icons-filter"  onClick={this.handleSide}>
      {this.state.category!=null ?this.state.category:'Filter Category'} <i className="bx bx-caret-down"/>
      </div>
   
    {/*   <div className="icons-count">
      
       {this.state.icons.data!==null ? this.state.icons.data.length:'0'} icons</div>
     */}   </div>
           </div>
</div>
         </IconsFilter>
       
              {this.state.selectedIcon.name != null && (
                <StyledIconCont>
                
                  {this.renderIcon()} 
                </StyledIconCont>
              )}
            </div>
          </div>{this.state.showSide &&
               <OutsideClickHandler onOutsideClick={() => {
                this.setState({ showSide: false });
            }}>
            <div className="icons-side-section ">

                <div className="side-section-inner"> 
                 <Categories changeCategories={this.props.changeCategories} search={this.state.search} style={this.state.style} />
                </div>
            </div>
            </OutsideClickHandler>
            }
          {this.state.icons.data != null && (
            <div className="icons-right-section ">
             {this.state.category===null&&
             <div>{this.state.search===''&&<div>
            {this.state.style==='ALL'&&
             <h2>{this.state.icons.data.length}  icons crafted with material guidelines  <BlueButton
             href={"/static/boxicons-"+VERSION+".zip"}
             >
             <div className="button-name">Download <span>v{VERSION}</span></div> 
                </BlueButton></h2>}</div>
            }</div>  }
              {this.state.noResult ? (
                <div className="icon-list ">
                  <div className="container">
                    <h3 className="no-result">
                      No result found  
                      {this.state.search!==''&&
                    <span> for  
                      <strong> '{this.state.search}'</strong></span> 
              }
                      {this.state.category!=null&& <span> in <strong>'{this.state.category}'</strong></span>}
                         <p>Having trouble finding an icon, try the <Link href="cheatsheet"><a>Cheatsheet</a></Link></p>
                 
                    </h3>
                  </div>
                </div>
              ):     <IconsList  icons={this.state.icons.data}  style={this.state.style}/>
            }
          <Footer/>
            </div>
          )}</IconsSectionCont>
        </IconsSection>
     
    );
  }
}
function mapStateToProps(state) {
  return {
    icons: state.icons,
    search: state.search,
    displaySideIcon: state.displaySideIcon,
    selectedIcon: state.selectedIcon,
    category: state.category,
    displayCategories: state.displayCategories,
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators(
    {
      fetchIcons,
      fetchStyle,
      changeSideIcon,
        setSelectedIcon,
        setSearchTerm,
        setCategory,
        changeCategories
    },
    dispatch
  );
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(IconsSet);